import React from "react";
import PropTypes from "prop-types";
import { updateMerchant } from "../../../redux/actions";
import { ToastContainer, toast } from 'react-toastify';
import axios from "axios";
import { baseURL } from "../../../redux/config";

import { store } from "../../../redux/Store.js";


const Monthdata = (props) => {

  let authData = { ...store.getState().auth.authData };

  let headers = { Authorization: "Bearer " + authData.accessToken };



  const acceptMerchant = (id, data) => {


    axios.post(`${baseURL}merchant/update?id=${id}`, data, { headers })
      .then(res => {
        if (res.data?.data) {
          props.set_list(props.list.filter(v => v._id != id))

          toast.success("Merchant Accepted")
        }
      })

      .catch(err => console.log(err))


  }


  

  const rejectMerchant = (id, data) => {


    axios.post(`${baseURL}merchant/update?id=${id}`, data, { headers })
      .then(res => {
        if (res.data?.data) {
          props.set_list(props.list.filter(v => v._id != id))

          toast.success("Request Rejected")
        }
      })

      .catch(err => console.log(err))


  }




  // render() {

  const image = (
    <img
      src={props.image}
      alt="user"
      width="50"
      className="rounded-circle"
    />
  );
  const name = <h6 className="font-medium mb-0">{props.name}</h6>;

  const bName = <small className="text-muted">{props.bName}</small>;
  const email = <div>{props.email}</div>;
  const status = (
    <span className={"badge badge-" + props.badgeColor}>
      {props.status}
    </span>
  );
  const budget = <div>{props.budget}</div>;


  return (

    <tr>

      {/* <td> */}
      {/* <span>{image}</span> */}
      {/* </td> */}
      <td>{name}</td>
      <td>{email}</td>
      <td>{bName}</td>
      <td>{status}</td>
      <td>{budget}</td>

      <button onClick={() => acceptMerchant(props.id, { userStatus: "active" })} type={"button"} className={"btn btn-success mr-3"}>
        Accept
      </button>
      <button onClick={() => rejectMerchant(props.id, { userStatus: "rejected" })} type={"button"} className={"btn btn-danger"}>
        Reject
      </button>
    </tr>
  );
  // }
}

Monthdata.defaultProps = {
  badgeColor: "primary",
};

Monthdata.propTypes = {
  badgeColor: PropTypes.oneOf([
    "primary",
    "success",
    "info",
    "danger",
    "warning",
    "orange",
    "cyan",
  ]),

  image: PropTypes.string,
  name: PropTypes.string,
  bName: PropTypes.string,
  email: PropTypes.string,
  status: PropTypes.string,
  budget: PropTypes.string,
};

export default Monthdata;
